import React from 'react';
import Chart from 'chart.js';
import styles from './LineGraph.module.css'

export default class LineGraph extends React.Component {
    chartRef = React.createRef();

    componentDidMount() {
        const myChartRef = this.chartRef.current.getContext("2d");


        new Chart(myChartRef, {
            type: "line",
            data: {
                //Bring in data
                labels: ["Jan", "Feb", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"],
                datasets: [
                    {
                        label: "patient in 2020",
                        data: [140, 110, 210, 150, 160, 138, 180, 171, 136, 215, 177, 250],
                        borderColor: 'rgba(86, 96, 232, 0.5)',
                        backgroundColor: 'rgba(64, 181, 245, 0.2)',
                    },
                    {
                        label: "patient in 2019",
                        data: [50, 80, 119, 100, 160, 128, 125, 145, 175, 107, 100, 187],
                        borderColor: 'rgba(232, 145, 23, 0.7)',
                    }
                ]
            },
            options: {
                //Customize chart options
                responsive: true,
                maintainAspectRatio: false,
                title: {
                    display: true,
                    text: 'Hospital Survey',
                    position: 'left',
                    fontSize: 20,
                },
            }
        });
    }

    render() {
        return (
            <div className={styles.graph}>
                <canvas
                    id="myChart"
                    ref={this.chartRef}
                />
            </div>

        )
    }
}