import styles from './Cards.module.css';
import firstAid from './../../../images/Appointments/cards/first-aid.png';
import crutch from './../../../images/Appointments/cards/crutch.png';
import operation from './../../../images/Appointments/cards/operation-theater.png';
import bank from './../../../images/Appointments/cards/bank.png';

const Cards = () => {
    return (
        <div className={styles.cards}>
            <div className={styles.pos}>
                <div className={styles.img}><img alt="" src={firstAid} /></div>
                <div className={styles.info}>
                    <div>Appointments</div>
                    <div>213</div>
                </div>
            </div>
            <div className={styles.pos}>
                <div className={styles.img}><img alt="" src={crutch} /></div>
                <div className={styles.info}>
                    <div>New Patients</div>
                    <div>104</div>
                </div>
            </div>
            <div className={styles.pos}>
                <div className={styles.img}><img alt="" src={operation} /></div>
                <div className={styles.info}>
                    <div>Operations</div>
                    <div>24</div>
                </div>
            </div>
            <div className={styles.pos}>
                <div className={styles.img}><img alt="" src={bank} /></div>
                <div className={styles.info}>
                    <div>Hospital Earning</div>
                    <div>$ 12,174</div>
                </div>
            </div>
        </div>
    );
}
export default Cards;