import React from 'react';
import Chart from 'chart.js';
import numeral from 'numeral';
import styles from './MonthIncome.module.css';
class LineGraphMonth extends React.Component {
    chartRef = React.createRef();

    componentDidMount() {
        const myChartRef = this.chartRef.current.getContext("2d");



        new Chart(myChartRef, {
            type: "line",
            data: {
                //Bring in data
                labels: ["1 July", "8 July", "16 July", "24 July", "31 July"],
                datasets: [
                    {
                        label: "Income in current month",
                        data: [49000, 79000, 33000, 63000, 41000],
                        borderColor: 'rgba(86, 96, 232, 0.5)',
                        backgroundColor: 'rgba(255,255,255,0)',
                    }
                ]
            },
            options: {
                //Customize chart options
                responsive: true,
                maintainAspectRatio: false,
                title: {
                    display: true,
                    text: '$ 100,000',
                    position: 'left',
                    fontSize: 20,
                },

                scales: {
                    yAxes: [{
                        ticks: {
                            callback: function (value, index, values) {
                                // add comma as thousand separator
                                return numeral(value).format('$ 0,0')
                            },
                            min: 0,
                            max: 100000,
                        }
                    }]
                },
                tooltips: {
                    callbacks: {
                        label: function (tooltipItem, data) {
                            var label = data.datasets[tooltipItem.datasetIndex].label || ''

                            if (label) {
                                label += ': '
                            }
                            label += numeral(tooltipItem.yLabel).format('$ 0,0')
                            return label
                        },
                    },
                },

            }
        });
    }

    render() {
        return (
            <div className={styles.graphMonth}>
                <canvas
                    id="myChart"
                    ref={this.chartRef}
                />
            </div>
        )
    }
}

export default LineGraphMonth;