import Cards from './Cards/Cards';
import styles from './Dashboard.module.css';
import React from 'react';
import LineGraph from './Graphs/LineGraph/LineGraph';
import IncomesContainer from './Graphs/GraphsIncomes/IncomesContainer';
import userIco from './../../images/Header/user.png';
import deleteIco from './../../images/Appointments/other/delete.png';
import editIco from './../../images/Appointments/other/edit.png';

let store = {
    _state: {
        users: [
            {id: 1, name: "Leslie Alexander", email: "lesie.alexander@example.com", 
            date: "10/10/2020", visitTime: "09:15-09:45am", doctor: "Dr. Jacob Jones", conditions: "Mumps Stage II"},

            {id: 2, name: "Ronald Richards", email: "ronald.richards@example.com", 
            date: "10/12/2020", visitTime: "12:00-12:45pm", doctor: "Dr. Theresa Webb", conditions: "Depression"},

            {id: 3, name: "Jane Cooper", email: "jane.cooper@example.com", 
            date: "10/13/2020", visitTime: "01:15-01:45pm", doctor: "Dr. Jacob Jones", conditions: "Arthritis"},

            {id: 4, name: "Robert Fox", email: "robert.fox@gmail.com", 
            date: "10/14/2020", visitTime: "02:00-02:45pm", doctor: "Dr. Arlene McCoy", conditions: "Fracture"},

            {id: 5, name: "Jenny Wilson", email: "jenny.wilson@example.com", 
            date: "10/15/2020", visitTime: "12:00-12:45pm", doctor: "Dr. Esther Howard", conditions: "Depression"}
        ]
    },
    getState() {
        return this._state;
    }
}

const User = (props) => {
    return(
    <div className={styles.user}>
        <div className={styles.marginAvatar}><img alt="" className={styles.avatar} src={userIco}/></div>
        <div className={styles.fullName}>
        {
            props.user.name
        }
        </div>
        <div className={styles.email}>
        {
            props.user.email
        }
        </div>
        <div className={styles.date}>
        {
            props.user.date
        }
        </div>
        <div className={styles.visitTime}>
        {
            props.user.visitTime
        }
        </div>
        <div className={styles.doctor}>
        {
            props.user.doctor
        }
        </div>
        <div className={styles.conditions}>
        {
            props.user.conditions
        }
        </div>
        <div className={styles.editIco}><img alt="" src={editIco}/></div>
        <div className={styles.delIco}><img alt="" src={deleteIco}/></div>
        
    </div>
    );
}

const getUsers = ({users}) => {
    return users
} 

const Users = ({users}) => {
    
    return (
        <div className={styles.users}>
            {
                users.map(u => <User key={u.id} user={u}/>)
            }
        </div>
    );
}

const AppointmentActivity = ({users}) => {
    
    return (
        <div className={styles.AppointDiv}>
            <div className={styles.NameSize}>Appointment Activity</div>
            <div className={styles.articles}>
                <div>Name</div>
                <div>Email</div>
                <div>Date</div>
                <div>Visit Time</div>
                <div>Doctor</div>
                <div>Conditions</div>
            </div>
            <Users users={getUsers(users)}/>
        </div>
    );
}

let setState = store.getState.bind(store);


const Dashboard = () => {
    return (
        <div className={styles.DashbDIV}>
            <Cards />
            <LineGraph />
            <IncomesContainer />
            <AppointmentActivity users={setState()} />
        </div>
    );
}

export default Dashboard;