import styles from './IncomesContainer.module.css';
import LineGraphMonth from './MonthIncome/MonthIncome';
import LineGraphWeek from './WeekIncome/WeekIncome';

const IncomesContainer = () => {
    return (
        <div className={styles.GraphsIncomes}>

            <LineGraphMonth />
            <LineGraphWeek />

        </div>
    );
}

export default IncomesContainer