import React from 'react';
import Chart from 'chart.js';
import numeral from 'numeral';
import styles from './WeekIncome.module.css'

class LineGraphWeek extends React.Component {
    chartRef = React.createRef();

    componentDidMount() {
        const myChartRef = this.chartRef.current.getContext("2d");



        new Chart(myChartRef, {
            type: "line",
            data: {
                //Bring in data
                labels: ["25 July", "26 July", "27 July", "28 July", "29 July", "30 July", "31 July"],
                datasets: [
                    {
                        label: "Income in current week",
                        data: [65000, 38000, 59000, 40000, 78000, 18000, 56000],
                        borderColor: 'rgba(232, 145, 23, 0.7)',
                        backgroundColor: 'rgba(255,255,255,0)',
                    }
                ]
            },
            options: {
                //Customize chart options
                responsive: true,
                maintainAspectRatio: false,
                title: {
                    display: true,
                    text: '$ 25,000',
                    position: 'left',
                    fontSize: 20,
                },

                scales: {
                    yAxes: [{
                        ticks: {
                            callback: function (value, index, values) {
                                // add comma as thousand separator
                                return numeral(value).format('$ 0,0')
                            },
                            min: 0,
                            max: 100000,
                        }
                    }]
                },
                tooltips: {
                    callbacks: {
                        label: function (tooltipItem, data) {
                            var label = data.datasets[tooltipItem.datasetIndex].label || ''

                            if (label) {
                                label += ': '
                            }
                            label += numeral(tooltipItem.yLabel).format('$ 0,0')
                            return label
                        },
                    },
                },

            }
        });
    }

    render() {
        return (
            <div className={styles.graphWeek}>
                <canvas
                    id="myChart"
                    ref={this.chartRef}
                />
            </div>
        )
    }
}
export default LineGraphWeek;